@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing:border-box;
}
body{
  font-family: 'Open Sans';
}

.edit-icon{
  fill:#111;
  -webkit-transform:scale(1.4);
          transform:scale(1.4);
  cursor: pointer;
}

.add{
  margin-bottom:5vh;
}

main{
  min-height:100vh;
  display:grid;
  place-items:center;
  padding:20vh;
  position:relative;
}
main.login img{
  width:100px;
}

main.home .projects{
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-gap:1rem;
  gap:1rem;
}

input, button, textarea {
  font-size:1rem;
  padding: .5rem;
  display:block;
  margin-bottom:1rem;
  cursor: pointer;
}
input, textarea{
  min-width:20rem;
  background:white;
}
button{
  color:white;
  background:black;
  min-width:6rem;
}

header{
    position:fixed;
    width: 100vw;
    height:10vh;
    background:black;
    display:grid;
    grid-auto-flow: column;
    justify-content:center;
    align-items:center;
    grid-gap:1rem;
    gap:1rem;
    z-index:1;
  }
  
  header a{
    text-decoration: none;
    color:#eee;
  }
  
  [aria-current="page"] {
    font-weight: bold;
  }
  
.project{
    min-height:350px;
    padding:2rem;
    background: #eee;
    position:relative;
  }

  .project h1{
    font-size:1.2rem;
  }

  .project .admin {
    position:absolute;
    bottom:1rem;
    right:1rem;
    display:grid;
    grid-auto-flow: column;
    grid-gap:1rem;
    gap:1rem;
}
 
